/* General App container */
.App {
  text-align: center;
  background-color: #f0f0f0; /* Fallback background for content area */
}

/* Hero Section - Fills the entire screen */
.hero-section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;

  /* 
    Placeholder background image. 
    Replace this URL with your own image in /react-app/public/images/ or use an external URL.
  */
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://images.unsplash.com/photo-1444703686981-a3abbc4d4fe3?q=80&w=2070&auto=format&fit=crop');
  background-size: cover;
  background-position: center;
}

.hero-text h1 {
  font-size: 3.5rem;
  font-weight: bold;
  margin: 0;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
}

.hero-text p {
  font-size: 1.25rem;
  margin-top: 1rem;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.7);
}

/* Content Section - Appears below the hero when scrolling */
.content-section {
  padding: 5rem 2rem;
  max-width: 800px;
  margin: 0 auto;
  color: #333;
  background-color: #fff;
  line-height: 1.8;
}

.content-section h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  border-bottom: 2px solid #f0f0f0;
  padding-bottom: 1rem;
}